$border-color: #2D324F;
$button-color: #FFC952;
@mixin clearfix() {
  &:before,
  &:after {
    content: "";
    display: table; }

  &:after {
    clear: both; } }

* {
  margin: 0;
  padding: 0;
  outline: none; }

.header {
  overflow: hidden;
  display: block; }

.cover {
  height: 100vh;
  // background:
  //// image: url('/img/infographic.png')
  //// position: center center
  //// size: cover
 }  //// repeat: no-repeat

.logo {
  margin-top: 15px;
  margin-left: 50px;
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  padding: 0 15px;
  font-size: 35px;
  border-left: 3px solid lighten(firebrick, 10%);
  border-top: 3px solid lighten(firebrick, 10%);
  border-right: 3px solid darken(firebrick, 20%);
  border-bottom:  3px solid darken(firebrick, 20%);
  text-shadow: 2px 2px 1px #222;
  background: {
    color: #800000;
    text-transform: uppercase; } }

.nav {
  display: block;
  min-width: 650px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  ul {
    display: flex;
    justify-content: flex-end;
    color: #fff;
    margin-top: 30px; }

  li {
    text-transform: uppercase;
    list-style: none;
    margin-right: 50px;

    a {
      font-size: 15px; } } }

.header__title {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  padding-top: 145px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);

  h1 {
    font-family: 'Abril Fatface';
    text-align: center;
    color: #ffffff;
    font-size: 60px;
    font-weight: bold; }

  a {
    margin-top: 30px;
    text-decoration: none;
    color: #ffffff;
    font-size: 15px;
    transition: all .3s ease-in-out;

    &:hover {
      color: rgba(255, 255, 255, 0.8); }

    i.fa {
      display: block;
      text-align: center;
      transition: all .3s ease-in-out; }

    &:hover > i.fa {
      transform: translate(0, 15px); } } }

#login {
  float: right;
  margin: 100px auto;
  margin-top: -20px;
  width: 45vw;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 30px 20px;
  text-align: center;
  color: #343838;
  font-weight: bold;

  .error {
    color: darkred; }

  input {
    padding: 10px;
    font-size: 14px;
    border: 1px solid $border-color;
    transition: all .3s ease-in-out;
    background-color: #fff;

    &:focus {
      box-shadow: inset 2px 5px 5px 0px #dddddd;
      background-color: #f5f5f5; }

    &:first-of-type {
      border-radius: 5px 5px 0 0; }

    &:last-of-type {
      border-top: 0;
      border-radius: 0 0 5px 5px; } }

  button {
    float: right;
    width: 100px;
    background-color: $button-color;
    border: 1px solid $border-color;
    margin-top: 25px;
    padding: 10px 15px;
    border-radius: 2px;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: lighten($button-color, 10%);
      box-shadow: 0px 0px 0px 1px rgb(45, 50, 79);
      color: rgb(45, 50, 79); } } }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  text-align: center;
  color: #fff;
  background-color: #34314c;

  .qdn {
    width: 90vw;

    h1 {
      font-weight: bold;
      color: #ffc952; }

    .subtitle {
      margin-top: -10px;
      margin-bottom: 30px; } } }
